<template>
    <div class="container">
        <v-back title="智能查询/修改" />
		<div class="btn_to_list" @click="toList">陌生人查询</div>
        <div class="search flex align_center">
			<md-field-item
			  style="width: 45vw;height: 8.5vw;"
			  :content="selectorValue"
			  @click="showSelector"
			  arrow
			/>
            <input type="text" placeholder="姓名/学号/工号" v-model="value">
            <md-button type="primary" size="small" inline @click="search">搜索一下</md-button>
        </div>
		<md-selector
		    v-model="isSelectorShow"
		    :data="data"
		    min-height="320px"
		    max-height="320px"
		    title="搜索类型"
		    okText="确认"
		    large-radius
		    @confirm="onSelectorConfirm"
		    >
		</md-selector>
        <div class="search_list">
            <div class="list">
                <div style="flex-wrap: wrap;" class="flex space-between">
                    <div v-for="(item, index) in list" :key="index" class="banner" @click="item.show = true">
						<div>
							<span :class="'usertype_title usertype_'+item.usertype"></span>
							<div>姓名:&nbsp;&nbsp;<span class="username">{{item.username || '--'}}</span></div>
							<div>用户类型:&nbsp;&nbsp;<span>{{typeText(item.usertype)}}</span></div>
							<div>学号/工号:&nbsp;&nbsp;<span>{{item.usernbr}}</span></div>
							<div v-if="item.usertype == 'S'">班级:&nbsp;&nbsp;<span>{{item.college_name  || '--'}}{{item.org_names}}{{item.class_names}}</span></div>
							<div v-if="item.usertype == 'S'">住宿:&nbsp;&nbsp;<span>{{item.building_name}}{{item.dorm_nbr}}间{{item.bed_nbr}}床</span></div>
							<div v-if="item.usertype == 'Y'">联系方式:&nbsp;&nbsp;<span>{{item.mobile}}</span></div>
							<div v-if="item.usertype == 'Y'">备注:&nbsp;&nbsp;<span>{{item.remark}}</span></div>
							<!-- <div v-if="item.usertype == 'S'">备注:&nbsp;&nbsp;<span>{{item.last_person_entry_exit ? (item.last_person_entry_exit.remark || '') : ''}}</span></div> -->
							<div>最后通行:&nbsp;&nbsp;<span style="font-size: 3.6vw;font-weight: bolder;" :style="item.last_person_entry_exit ? (item.last_person_entry_exit.entry_exit_type == '1' ? 'color: #1acfc6;' : item.last_person_entry_exit.entry_exit_type == '2' ? 'color: red;' : 'color: #ccc;') : 'color: #ccc;'">({{item.last_person_entry_exit ? (item.last_person_entry_exit.entry_exit_type == '1' ? '进' : item.last_person_entry_exit.entry_exit_type == '2' ? '出' : '未知') : '未知'}})</span><span>{{item.last_person_entry_exit?item.last_person_entry_exit.snap_camera_name : ''}} {{item.last_person_entry_exit ? (item.last_person_entry_exit.entry_exit_time || '--') : '--'}}</span></div>
							<div v-if="item.usertype == 'S'">状态:&nbsp;&nbsp;<span :class="statusText(item) == '在寝' ? 'in_status' : statusText(item) == '未归' ? 'out_status' : 'undiefind_status'">{{statusText(item)}}</span><i class="iconfont icon-shezhi"></i></div>
							<i class="md-icon icon-font lg" :class="item.show ?'active':''" @click.stop="item.show = !item.show">+</i>
							<div class="md_btns" v-show="item.show" @click.stop="item.show = false">
								<h3 style="color: #2c3e50;">{{item.username || '--'}}</h3>
								<div class="md_div">
									<span class="bgcolor-8" @click.stop="landShow(item)" v-show="item.usertype == 'S'">修改状态</span>
									<span class="bgcolor-6" @click.stop="toRouterSet(item)">修改个人信息</span>
									<span class="bgcolor-3" @click.stop="toRecord(item)">通行记录</span>
								</div>
							</div>
						</div>
                    </div>
                </div>
				<div @click="addMore" v-show="list.length > 0" :class="total > list.length ? 'class_more' : 'class_no_more'">{{total > list.length ? '加载更多' : '没有更多数据了'}}</div>
            </div>
			<div v-if="list.length == 0" class="no_data">
			    <md-result-page text="没内容？快去搜索一下"></md-result-page>
			</div>
            <md-landscape v-model="showFullScreen" :full-screen="true">
				<div style="text-align: left;padding: 4vw;font-size: 4vw;line-height: 6vw;">
				    <div style="margin-bottom: 4vw;">详细信息</div>
				    <div class="flex align_center" style="background-color: #1acfc6;box-sizing: border-box;padding: 2vw;border-radius: 1vw;">
				        <img v-if="showData.photo" :src="showData.photo" alt="" style="box-sizing: border-box;width: 30vw;margin: 0 auto;display: block;box-shadow: 2px 2px 5px rgba(0,0,0,.2);border: 1px solid #f5f5f5;"  @click="imgsShow(showData.photo)">
				        <img v-else src="@/assets/student.png" alt="" style="box-sizing: border-box;width: 30vw;margin: 0 auto;display: block;box-shadow: 2px 2px 5px rgba(0,0,0,.2);border: 1px solid #f5f5f5;">
						<div class="flex btns">
						    <div>
						        <div @click="statusChange(1)">在寝</div>
						    </div>
						    <div>
						        <div @click="statusChange(0)">未归</div>
						    </div>
						    <div>
						        <div @click="statusChange">实习</div>
						    </div>
						    <div>
						        <div @click="statusChange">请假</div>
						    </div>
							<div>
							    <div @click="statusChange">毕业</div>
							</div>
						</div>
					</div>
					<div class="person_list_text">
						<div class="flex">
							状态：
							<div class="full_status">
								{{ personStatusText(showData) }} | 
								<span :class="showData.last_person_entry_exit == null ? 'noactive' : showData.last_person_entry_exit.in_dorm_status == '0' ? '' : 'active'">{{statusText(showData)}}</span>
							</div>
						</div>
						<div class="flex align_center" v-show="personStatusText(showData) != '正常'">请假/实习：{{aroundTime}} <md-icon name="id-card" size="lg" @click="imgsShow(showData.person_io_leave_practices[0].photo)"></md-icon></div>
						<div class="flex">抓拍图片：<img v-if="showData.last_person_entry_exit && showData.last_person_entry_exit.snap_photo" style="width: 15vw;" @click="imgsShow(showData.last_person_entry_exit.snap_photo)" :src="showData.last_person_entry_exit.snap_photo" alt=""></div>
						<div>学生姓名：{{showData.username}}</div>
						<div>学生学号：{{showData.usernbr}}</div>
						<div>学院名称：{{showData.org_names}}</div>
						<div>班级名称：{{showData.class_names || '--'}}</div>
						<div>住宿信息：{{showData.building_name}}{{showData.dorm_nbr || '--'}}间{{showData.bed_nbr || '--'}}床</div>
						<div>最后通行：<span style="font-size: 3.6vw;font-weight: bolder;" :style="showData.last_person_entry_exit ? (showData.last_person_entry_exit.entry_exit_type == '1' ? 'color: #1acfc6;' : showData.last_person_entry_exit.entry_exit_type == '2' ? 'color: red;' : 'color: #ccc;') : 'color: #ccc;'">({{showData.last_person_entry_exit ? (showData.last_person_entry_exit.entry_exit_type == '1' ? '进' : showData.last_person_entry_exit.entry_exit_type == '2' ? '出' : '未知') : '未知'}})</span>{{showData.last_person_entry_exit ? showData.last_person_entry_exit.snap_camera_name : ''}} {{showData.last_person_entry_exit ? showData.last_person_entry_exit.entry_exit_time : '--'}}</div>
						<div>备注：{{showData.last_person_entry_exit ? showData.last_person_entry_exit.remark : '--'}}</div>
						<md-button type="primary" size="small" round @click="toRecord2(showData)" style="margin-top: 4vw;">通行记录</md-button>
					</div>
				</div>
            </md-landscape>
        </div>
        <!-- 图片预览 -->
        <md-image-viewer
        	v-model="isViewerShow"
        	:list="imgs"
        	:has-dots="true">
        </md-image-viewer>
    </div>
</template>

<script>
	var dayjs = require('dayjs')
    export default {
        name: 'search_index',
        data() {
            return {
                value: '',
                list: [],
                total: 0,
                showFullScreen: false,
                showData: {
					last_person_entry_exit: {}
				},
                page: 0,
				aroundTime: '',
				
				isViewerShow: false,
				imgs: [],
				
				
				selectorValue: '全部',
				usertype: '',
				isSelectorShow: false,
				data: [
					{
						value: '',
						text: '全部'
					},
					{
						value: 'S',
						text: '学生'
					},
					{
						value: 'T',
						text: '教师'
					},
					{
						value: 'T1',
						text: '辅导员'
					},
					{
						value: 'V',
						text: '访客'
					},
					{
						value: 'Y',
						text: '黄名单'
					}
				]
            }
        },
        methods: {
			toList() {
				this.$router.push('/search/list')
			},
			showSelector() {
			    this.isSelectorShow = true
			},
			onSelectorConfirm({text, value}) {
				this.selectorValue = text
				this.usertype = value
			    console.log(text)
				console.log(value)
				this.getList()
			},
			typeText(usertype) {
				if (usertype == 'S') {
					return '学生'
				} else if (usertype == 'T') {
					return '老师'
				} else if (usertype == 'V') {
					return '访客'
				} else if (usertype == 'Y') {
					return '黄名单人员'
				} else if (usertype == 'U') {
					return '系统管理员'
				} else if (usertype == 'I') {
					return '陌生人'
				} else if (usertype == 'T1') {
					return '辅导员'
				} else {
					return '其他'
				}
			},
			personStatusText(item) {
				let text = '无记录'
				if (item.last_person_entry_exit) {
					if (item.last_person_entry_exit.entry_exit_status != null) {
						if (item.last_person_entry_exit.entry_exit_status == '0') {
							text = '正常'
						}
					}
				}
				if (item.person_io_leave_practices && item.person_io_leave_practices.length > 0) {
					this.aroundTime = dayjs(item.person_io_leave_practices[0].no_attend_starttime).format('YYYY-MM-DD') + ' ~ ' + dayjs(item.person_io_leave_practices[0].no_attend_endtime).format('YYYY-MM-DD')
					if (item.person_io_leave_practices[0].type == 1) {
						text = '请假'
					} else {
						text = '实习'
					}
				}
				return text
			},
			statusText(item) {
				if (item.last_person_entry_exit) {
					if (item.last_person_entry_exit.in_dorm_status == null) {
						return '无记录'
					} else {
						if (item.last_person_entry_exit.in_dorm_status == '0') {
							return '未归'
						} else {
							if (item.last_person_entry_exit.in_dorm_status == '1') {
								return '在寝'
							} else {
								return '未知'
							}
						}
					}
				} else {
					return '无记录'
				}
			},
			imgsShow(img) {
				this.isViewerShow = true
				this.imgs = [img]
			},
            landShow(item) {
                this.showData = item
                this.showFullScreen = true
            },
            statusChange(status) {
                this.$getL('/dorm/last_change', {
                    userId: JSON.parse(localStorage.getItem('user')).user_id,
					username: JSON.parse(localStorage.getItem('user')).username,
                    studentNbr: this.showData.usernbr,
					entry_exit_time: this.showData.last_person_entry_exit ? this.showData.last_person_entry_exit.entry_exit_time : '',
                    status: status
                }, (res)=>{
                    this.$toast.succeed('修改成功')
					let in_dorm_status = '2'
					if (this.showData.last_person_entry_exit) {
						in_dorm_status = this.showData.last_person_entry_exit.in_dorm_status
					}
					let remark = dayjs().format('YYYY-MM-DD HH:mm:ss') + ' ' + JSON.parse(localStorage.getItem('user')).username + ' 手动将人员在寝状态由' + (in_dorm_status == 0 ? '【未归】' : in_dorm_status == 1 ? '【在寝】' : '【未知】') + '修改为' + (status == 0 ? '【未归】' : '【在寝】')
					if (this.showData.last_person_entry_exit) {
						this.showData.last_person_entry_exit.in_dorm_status = status
						this.showData.last_person_entry_exit.remark = remark
						this.showData.last_person_entry_exit.entry_exit_status = 0
					} else {
						this.showData.last_person_entry_exit = {
							in_dorm_status: status,
							entry_exit_status: 0,
							remark: remark
						}
					}
                    setTimeout(() => {
                    	this.$toast.hide()
                    }, 500)
                })
            },
            search() {
				localStorage.setItem('search_keyname', this.value)
                this.page = 0;
                this.getList()
            },
            getList() {
                if (this.value == '') {
                    this.$toast.failed('请输入搜索内容')
                } else {
                    this.$getL('/search/list', {
                        userId: JSON.parse(localStorage.getItem('user')).user_id,
                        keyname: this.value,
                        page: this.page,
						usertype: this.usertype
                    }, (res)=>{
						let list = res.data.rows
						list.forEach(item => {
							item.show =false
						})
                        if (this.page > 0) {
                            this.list = this.list.concat(list)
                        } else {
                            this.list = list
                        }
                        this.total = res.data.count
						if (this.total == 0) {
							this.$toast.failed('换个搜索内容试试！！！')
						} else {
							this.$toast.hide()
						}
                    })
                }
            },
            addMore() {
                if (this.total > this.list.length) {
                    this.page++
                    this.getList()
                }
            },
			toRouterSet(item) {
				if (item.usertype == 'S' || item.usertype == 'T') {
					this.$router.push('/add_ts/index?type=set&usertype=' + item.usertype + '&usernbr=' + item.usernbr)
				} else if (item.usertype == 'V') {
					this.$router.push('/add/index?type=set&usernbr=' + item.usernbr)
				} else if (item.usertype == 'Y') {
					this.$router.push('/add_yellow/index?type=set&usernbr=' + item.usernbr)
				}  else {
					//进入陌生人详情页面
					this.$router.push('/add_ts/index?type=set&usertype=' + item.usertype + '&usernbr=' + item.usernbr)
				}
			},
			toRecord2(item) {
				this.$router.push('/record/index?usernbr=' + item.usernbr + '&username=' + item.username)
			},
			toRecord(item) {
				this.$router.push('/record/index?usernbr=' + item.usernbr + '&username=' + item.username)
			}
        },
        mounted() {
			let keyname = localStorage.getItem('search_keyname')
            if (keyname && keyname != '') {
				this.value = keyname
				this.getList()
			}
        }
    }
</script>

<style scoped>
    .search {
        padding: 4vw;
    }
    .search input {
        box-sizing: border-box;
        height: 8.5vw;
        width: 100%;
        padding: 0 2vw;
        outline: none;
        font-size: 3.5vw;
        border-width: 0;
    }
    .search_list {
        height: 82vh;
        font-size: 3.5vw;
		overflow-y: auto;
		padding: 2px 0;
    }
    .list {
        
		padding: 0 4vw;
        position: relative;
    }
    .no_data {
        width: 100%;
        position: absolute;
        top: 45%;
        transform: translateY(-50%);
    }
	.banner {
		background-color: #fff;
		width: calc(50% - 2vw);
		border-radius: 1vw;
		margin-bottom: 4vw;
		box-shadow: 5px 5px 10px rgba(0,0,0,.1);
		color: rgba(0,0,0,.5);
		position: relative;
		overflow: hidden;
		min-height: 70vw;
	}
    .banner div{
        padding: 1vw;
    }
	.banner .md-icon {
		position: absolute;
		width: 8vw;
		height: 8vw;
		bottom: 2vw;
		right: 2vw;
		font-size: 7vw;
		box-shadow: 2px 2px 5px rgba(0,0,0,.3);
		border-radius: 50%;
		text-align: center;
		line-height: 8vw;
		z-index: 2;
	}
	.banner .md-icon.active {
		transform: rotateZ(45deg);
	}
	.banner span {
		color: #2c3e50;
		font-weight: bolder;
	}
	.banner span.username {
		font-weight: bolder;
		font-size: 4.6vw;
	}
	.banner_title {
		font-size: 3.5vw;
	}
	.usertype_title {
		display: block;
		border-radius: 1vw 1vw 0 0;
		padding: 1vw;
		margin: -1vw -1vw 0 -1vw;
		background-color: #666;
	}
	.usertype_S {
		background-color: #1acfc6;
	}
	.usertype_T {
		background-color: cornflowerblue;
	}
	.usertype_V {
		background-color: #ccc;
	}
	.usertype_Y {
		background-color: yellow;
	}
	.usertype_U {
		background-color: #666;
	}
	.md_btns {
		position: absolute;
		top: 2vw;
		right: 0;
		left: 0;
		bottom: 0;
		z-index: 1;
		background-color: #fff;
	}
	.md_div {
		position: absolute;
		width: calc(100% - 4vw);
		top: 50%;
		transform: translateY(-50%);
	}
	.md_btns span {
		border-radius: 50%;
		width: 18vw;
		height: 18vw;
		display: flex;
		text-align: center;
		margin: 3vw auto;
		color: #fff;
		align-items: center;
		justify-content: center;
		font-size: 3.5vw;
		box-sizing: border-box;
		padding: 1vw;
		box-shadow: 0px 0px 10px rgba(0,0,0,.2);
	}
	.md-field-item >>> .md-field-item-content {
		    padding: 0 2vw;
		    min-height: 8.5vw;
	}
	.md-field-item >>> .md-field-item-right .md-icon-arrow-right {
		transform: rotate(90deg);
	}
	.btn_to_list {
		position: absolute;
		top: 0px;
		right: 0px;
		color: rgb(255, 255, 255);
		font-size: 3.6vw;
		line-height: 8vw;
		padding: 0 2vw;
		margin: 3vw 4vw;
		height: 8vw;
		box-shadow: 2px 2px 5px rgba(0,0,0,.2);
		border-radius: 1vw;
	}
</style>